// Slider

function slider() {
  const slides = document.querySelectorAll(".offer__slide"),
    slider = document.querySelector(".offer__slider"),
    prev = document.querySelector(".offer__slider-prev"),
    next = document.querySelector(".offer__slider-next"),
    total = document.querySelector("#total"),
    current = document.querySelector("#current"),
    slidesWrapper = document.querySelector(".offer__slider-wrapper"),
    slidesField = document.querySelector(".offer__slider-inner"),
    width = window.getComputedStyle(slidesWrapper).width;

  let slideIndex = 1;
  let offset = 0;

  slidesWrapper.style.overflow = "hidden";
  slidesField.style.width = 100 * slides.length + "%";
  slidesField.style.display = "flex";
  slidesField.style.transition = ".45s all";
  slides.forEach((slide) => {
    slide.style.width = width;
  });
  slider.style.position = "relative";
  const carouselDots = document.createElement("ol"),
    dots = [];
  carouselDots.classList.add("carousel-dots");
  slider.append(carouselDots);
  for (let i = 0; i < slides.length; i++) {
    const dot = document.createElement("li");
    dot.setAttribute("data-slide-to", i + 1);
    dot.classList.add("dot");
    if (i == 0) dot.classList.add("dot-active");
    carouselDots.append(dot);
    dots.push(dot);
  }

  total.textContent = slides.length < 10 ? `0${slides.length}` : slides.length;
  current.textContent = slideIndex < 10 ? `0${slideIndex}` : slideIndex;
  function onlyDigits(str) {
    return str.replace(/\D/gi, "");
  }
  prev.addEventListener("click", () => {
    if (offset == 0) {
      offset = +onlyDigits(width) * (slides.length - 1);
    } else {
      offset -= +onlyDigits(width);
    }
    slideIndex == 1 ? (slideIndex = slides.length) : slideIndex--;
    translateSlide();
  });
  next.addEventListener("click", () => {
    if (offset == +onlyDigits(width) * (slides.length - 1)) {
      offset = 0;
    } else {
      offset += +onlyDigits(width);
    }

    slideIndex == slides.length ? (slideIndex = 1) : slideIndex++;
    translateSlide();
  });

  dots.forEach((dot) => {
    dot.addEventListener("click", (e) => {
      const slideTo = e.target.getAttribute("data-slide-to");
      slideIndex = slideTo;
      offset = +width.slice(0, width.length - 2) * (slideTo - 1);
      translateSlide();
    });
  });
  function translateSlide() {
    slidesField.style.transform = `translateX(-${offset}px)`;
    current.textContent = slideIndex < 10 ? `0${slideIndex}` : slideIndex;
    dots.forEach((dot) => dot.classList.remove("dot-active"));
    dots[slideIndex - 1].classList.add("dot-active");
  }
  // function showSlides(n) {
  //   if (n > slides.length) {
  //     slideIndex = 1;
  //   } else if (n < 1) {
  //     slideIndex = slides.length;
  //   }
  //   current.textContent = slideIndex < 10 ? `0${slideIndex}` : slideIndex;
  //   slides.forEach((slide) => {
  //     slide.classList.add("hide");
  //     slide.classList.remove("fade");
  //   });
  //   slides[slideIndex - 1].classList.replace("hide", "show");
  //   slides[slideIndex - 1].classList.add("fade");
  // }
  // function slideStep(n) {
  //   showSlides((slideIndex += n));
  // }
  // prev.addEventListener("click", () => {
  //   slideStep(-1);
  // });
  // next.addEventListener("click", () => {
  //   slideStep(1);
  // });
  // showSlides(1);
}

// module.exports = slider;
export default slider;
