// calculator
function calculator() {
  const result = document.querySelector(".calculating__result>span");
  let sex = localStorage.getItem("sex") || "female",
    height,
    weight,
    age,
    ratio = +localStorage.getItem("ratio") || 1.375;

  function initLocalSetting(selector, activeClass) {
    const elements = document.querySelectorAll(`${selector} div`);
    elements.forEach((elem) => {
      elem.classList.remove(activeClass);
      if (elem.getAttribute("id") === sex) {
        elem.classList.add(activeClass);
      }
      console.log(+elem.getAttribute("data-ratio"));
      if (+elem.getAttribute("data-ratio") === ratio) {
        elem.classList.add(activeClass);
      }
    });
  }
  initLocalSetting("#gender", "calculating__choose-item_active");
  initLocalSetting(
    ".calculating__choose_big",
    "calculating__choose-item_active"
  );
  function calcTotal() {
    if (!sex || !height || !weight || !age || !ratio) {
      result.textContent = "____";
      return;
    }
    if (sex == "female") {
      result.textContent = Math.round(
        (447.6 + 9.2 * weight + 3.1 * height - 4.3 * age) * ratio
      );
    } else {
      result.textContent =
        //((88.36 + 13.4 * weight + 4.8 * height - 5.7 * age) * ratio).toFixed(0);
        Math.round((88.36 + 13.4 * weight + 4.8 * height - 5.7 * age) * ratio);
    }
  }
  calcTotal();
  function getStaticInfromation(parentSelector, activeClass) {
    const elements = document.querySelectorAll(`${parentSelector} div`);
    document.querySelector(parentSelector).addEventListener("click", (e) => {
      if (!e.target.classList.contains("calculating__choose-item")) return;
      if (e.target.hasAttribute("data-ratio")) {
        ratio = +e.target.getAttribute("data-ratio");
        localStorage.setItem("ratio", +e.target.getAttribute("data-ratio"));
      } else {
        sex = e.target.getAttribute("id");
        localStorage.setItem("sex", e.target.getAttribute("id"));
      }
      console.log(ratio, sex);

      elements.forEach((elem) => {
        elem.classList.remove(activeClass);
      });
      e.target.classList.add(activeClass);
      calcTotal();
    });
  }
  getStaticInfromation("#gender", "calculating__choose-item_active");
  getStaticInfromation(
    ".calculating__choose_big",
    "calculating__choose-item_active"
  );
  function getInputInformation(selector) {
    const input = document.querySelector(selector);
    input.addEventListener("input", (e) => {
      if (input.value.match(/\D/g)) {
        input.style.border = "1px solid red";
      } else {
        input.style.border = "none";
      }
      switch (input.getAttribute("id")) {
        case "height":
          height = +input.value;
          break;
        case "weight":
          weight = +input.value;
          break;
        case "age":
          age = +input.value;
          break;
      }
      calcTotal();
    });
  }
  getInputInformation("#height");
  getInputInformation("#weight");
  getInputInformation("#age");
}

// module.exports = calculator;
export default calculator;
