// Modal
function modal() {
  const modalButtons = document.querySelectorAll("[data-modal]"),
    // exitModalButtons = document.querySelectorAll("[data-close]"),
    modalDiv = document.querySelector(".modal");
  const modalTimerId = setTimeout(openModal, 50000);
  function openModal() {
    modalDiv.classList.add("show", "fade");
    document.body.style.overflow = "hidden";
    clearInterval(modalTimerId);
  }
  function closeModal() {
    console.log("CloseModal");
    modalDiv.classList.remove("show", "fade");
    document.body.style.overflow = "inherit";
  }
  modalButtons.forEach((button) => {
    button.addEventListener("click", openModal);
  });
  // exitModalButtons.forEach((exitModalButton) => {
  //   exitModalButton.addEventListener("click", closeModal);
  // });
  modalDiv.addEventListener("click", (e) => {
    if (e.target === modalDiv || e.target.hasAttribute("data-close")) {
      closeModal();
    }
  });
  document.addEventListener("keydown", (e) => {
    if (modalDiv.classList.contains("show") && e.code === "Escape")
      closeModal();
  });
  function showModalbyScroll() {
    if (
      window.pageYOffset + document.documentElement.clientHeight >=
      document.documentElement.scrollHeight - 1
    ) {
      openModal();
      window.removeEventListener("scroll", showModalbyScroll);
    }
  }
  window.addEventListener("scroll", showModalbyScroll);
  const message = {
    loading: "icons/spinner.svg",
    success: "Спасибо! Скоро мы свяжемся с вами",
    failure: "Что-то пошло не так...",
  };
  // доработка форм
  const forms = document.querySelectorAll("form");
  function showThanksModal(message) {
    openModal();
    const prevModalDialog = document.querySelector(".modal__dialog");
    prevModalDialog.classList.add("hide");
    const thanksModal = document.createElement("div");
    thanksModal.classList.add("modal__dialog");
    thanksModal.innerHTML = `
    <div class='modal__content'>
      <div class="modal__close" data-close>×</div>
      <div class="modal__title">${message}</div>
    </div>
    `;

    document.querySelector(".modal").append(thanksModal);
    setTimeout(() => {
      thanksModal.remove();
      prevModalDialog.classList.replace("hide", "show");
      closeModal();
    }, 4000);
  }
  const postData = async (url, data) => {
    const res = await fetch(url, {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: data,
    });
    console.dir(res);
    return await res.json();
  };
  function bindPostData(form) {
    form.addEventListener("submit", (e) => {
      e.preventDefault();

      // const statusMessage = document.createElement("div");
      // statusMessage.classList.add("status");
      // statusMessage.textContent = message.loading;
      const statusMessage = document.createElement("img");
      statusMessage.src = message.loading;
      statusMessage.style.cssText = `
      display: block;
      margin: 0 auto;
      `;
      // form.append(statusMessage);
      form.insertAdjacentElement("afterend", statusMessage);

      // const request = new XMLHttpRequest();
      // request.open("POST", "server.php");
      const formData = new FormData(form);

      // const object = {};
      // formData.forEach(function (value, key) {
      //   object[key] = value;
      // });

      const json = JSON.stringify(Object.fromEntries(formData.entries()));

      // fetch("server.php", {
      //   method: "POST",
      //   headers: {
      //     "Content-type": "application/json", //"multipart/form-data"
      //   },
      //   body: JSON.stringify(object), //formData,
      // })
      postData("http://localhost:3000/requests", json)
        .then((data) => {
          console.log(data);
          statusMessage.remove();
          showThanksModal(message.success);
        })
        .catch(() => {
          showThanksModal(message.failure);
        })
        .finally(() => {
          form.reset();
        });

      // multipart/form-data НЕ НАДО УКАЗЫВАТЬ В XMLHttpRequest, ОН АВТОМАТИЧЕСКИ ПОНИМАЕТСЯ
      // request.setRequestHeader("Content-type", "multipart/form-data");

      // request.setRequestHeader("Content-type", "application/json");
      // const object = {};
      // formData.forEach(function (value, key) {
      //   object[key] = value;
      // });

      // //request.send(formData);
      // request.send(JSON.stringify(object));
      // request.addEventListener("load", () => {
      //   if (request.status === 200) {
      //     console.log(request.response);
      //     // statusMessage.textContent = message.success;
      //     showThanksModal(message.success);
      //     form.reset();
      //   } else {
      //     console.error(request.statusText);
      //     // statusMessage.textContent = message.failure;
      //     showThanksModal(message.failure);
      //   }
      //   statusMessage.remove();
      //   // setTimeout(()=>{statusMessage.remove()},2000);
      // });
    });
  }
  forms.forEach((form) => {
    bindPostData(form);
  });
}

// module.exports = modal;
export default modal;
